.hero-component-container {
  box-sizing: content-box;
  padding: 20px;
  margin-top: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  min-height: 200px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);

  h2 {
    margin: 0;
    text-align: center;
  }

  .my-heroes-container {
    h2 {
      font-weight: 300;
      margin: 0;
    }
    display: flex;
    justify-content: start;
    flex-direction: column;

    border-radius: 5px;
    padding: 0 25px;
    position: relative;
    min-height: 200px;

    app-hero-form {
      width: 100%;
    }

    button {
      border: none;
      font-size: 14px;
    }

    .heroes {
      margin: 0;
      list-style-type: none;
      padding: 0;
      width: 100%;
      // padding: 10px 5px;
      border-radius: 5px;
      box-sizing: border-box;
      margin-top: 10px;
      height: auto;
      min-height: 200px;

      li {
        position: relative;
        cursor: pointer;
        background-color: azure;
        height: auto;
        align-items: center;
      }

      li:hover {
        left: 0.1em;
      }

      a {
        color: #333;
        text-decoration: none;
        background-color: #eee;
        margin: 0.5em;
        padding: 0.3em 0;
        height: 1.6em;
        border-radius: 4px;
        display: block;
        width: 99%;

        span {
          background-color: #104781;
        }
      }

      a:hover {
        color: #2c3a41;
        background-color: #e6e6e6;
      }

      a:active {
        background-color: #525252;
        color: #fafafa;
      }

      .badge {
        display: inline-block;
        font-size: small;
        color: white;
        padding: 0.8em 0.7em 0 0.7em;
        background-color: #405061;
        line-height: 1em;
        position: relative;
        left: -1px;
        top: -4px;
        height: 1.8em;
        min-width: 16px;
        text-align: right;
        margin-right: 0.8em;
        border-radius: 4px 0 0 4px;
      }
    }
  }

  @media (min-width: 600px) {
    .heroes {
      a {
        color: #333;
        text-decoration: none;
        background-color: #eee;
        margin: 0.5em;
        padding: 0.3em 0;
        height: 1.6em;
        border-radius: 4px;
        display: block;
        width: 25em;
      }
    }

    .my-heroes-container {
      flex-direction: column;
      app-hero-form {
        // background-color: aquamarine;
        width: 100%;
      }

      &.notFoundHero {
        display: flex;
        align-items: center;
        justify-content: center;
      }

      p {
        color: #a3a3a3;
        font-size: 18px;
      }
    }
  }
}
